/* src/App.css */

.App {
  text-align: center;
  /* CHANGE THIS: This is your background color */
  background-color: #161718; 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.game-grid {
  display: flex;
  gap: 20px;
  margin-top: 30px;
}

.game-card {
  padding: 20px 40px;
  font-size: 1.2rem;
  /* CHANGE THIS: The button color */
  background-color: #4CAF50; 
  /* CHANGE THIS: The text color on the button */
  color: white; 
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.2s, background-color 0.3s;
  font-weight: bold;
}

.game-card:hover {
  transform: scale(1.1);
  /* CHANGE THIS: The color when you hover over the button */
  background-color: #45a049; 
}